// FORMS

// modal background
.background {
    display: none;
    background-color: #0e1318af;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 5;
}

.edit {
    padding: 18px;

    &:hover {
        background-color: greenyellow;
        cursor: pointer;
    }
}

// FORM
form {
    z-index: 8;

    display: none;
    position: absolute;
    width: 80%;
    // height: 80%;
    // margin: 36px 0;
    top: 36px;
    // top: calc(36px);
    left: calc(10% - 36px);
    padding: 36px;
    flex-direction: column;
    background-color: $white;
    border-radius: $big-radius;

    &.active {
        display: flex;
    }

    &#create-task-form {
        section {
            display: grid;
            grid-template-columns: 4fr 3fr 2px;
            column-gap: 24px;
            input {
                width: 60%;

                &[name=taskEstimatedTimeHr],
                &[name=taskEstimatedTimeMin] {
                    width: 30%;
                }

                &[type=radio] {
                    width: 0;
                    margin: 0;
                    ~ span {
                        padding: 12px 18px;
                        display: flex;

                        div {
                            width: 12px;
                            height: 12px;
                            border-radius: 12px;
                            background-color: rgb(245, 146, 115);
                        }
                    }
                    &:checked ~ span {
                        background-color: $black;
                        color: $white;
                    }
                }
            }
            label.false-radio {
                margin: 0;
                padding: 0;
            }

            div:first-child {
                input {
                    width: 80%;
                }
            }
        }

        .form-button-container {
            display: flex;
            flex-direction: row-reverse;
            padding-top: 24px;
            margin-right: 10px;
        }
        button {
            align-self: flex-end;
        }
    }

    h1 {
        font-size: 24px;
        font-weight: 500;
    }

    button {
        &#edit-task-delete,
        &#edit-content-delete {
            display: none;
        }

        &.close-form {
            position: absolute;
            top: 24px;
            right: 24px;
            padding: 12px;
            display: flex;
            justify-content: center;

            background-color: $white;
            border-radius: $mini-radius;

            &:hover {
                background-color: $light-grey;
                cursor: pointer;
            }
        }
    }

    &.update {
        button {
            &#edit-task-delete,
            &#edit-content-delete {
                display: flex;
            }
        }
    }

    input {
        border: none;
        filter: drop-shadow(0 0 1px rgba(48, 48, 48, 0.18));
        border-radius: $mini-radius;
        padding: 12px;

        &:focus {
            border: none;
            filter: drop-shadow(0 0 2px #30303048);
        }
    }

    .radio-container {
        display: flex;
        margin-top: 18px;
        margin-bottom: 36px;
    }

    label {
        display: block;
        font-size: 12px;
        text-transform: uppercase;
        padding-left: 12px;
        margin: 24px 0 4px 0;
        color: $dark-grey;

        &[for="taskPriorityRating"] {
            display: flex;
            flex-direction: column;
        }

        .false-radio {
            &:hover {
                cursor: pointer;
                span {
                    color: $black;
                }
            }
            span {
                background-color: $white;
                color: $dark-grey;
                padding: 16px;
            }
        }
    }
}
